.footer{
  position: relative;
  color: $footer-txt-color;
  line-height: 1.71;
  border-top: 1px solid $footer-top-border-color;
  &:after {
    content:"";
    height: 1px;
    background-color: lighten($footer-top-border-color, 16%);
    width: 100%;
    position: absolute;
    top: -2px;
    left:0;
  }
}

.footer ul{
  list-style: none;
  padding:0;
  margin:0;
}

.footer ul li a{
  color: $footer-link-color;
}

.footer ul .active a,
.footer ul li a:hover{
  color: $footer-link-hover;
  text-decoration: none;
}

.footer-flex{
  margin-top: -4rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-between;
  -ms-flex-pack: space-between;
  justify-content: space-between;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.footer-flex .flex-item{
  margin-top: 4rem;
  -webkit-flex: 0 0 100%;
  -ms-flex: 0 0 100%;
  flex: 0 0 100%;
}
